input:not([type]),select, textarea , input[type=text]:not(.browser-default), input[type=password]:not(.browser-default), input[type=email]:not(.browser-default), input[type=url]:not(.browser-default), input[type=time]:not(.browser-default), input[type=date]:not(.browser-default), input[type=datetime]:not(.browser-default), input[type=datetime-local]:not(.browser-default), input[type=tel]:not(.browser-default), input[type=number]:not(.browser-default), input[type=search]:not(.browser-default), textarea.materialize-textarea {
  font-size: 1rem;
  box-sizing: content-box;
  width: 100%;
  height: 3rem;
  margin: 0 0 8px 0;
  padding: 0;
  -webkit-transition: border .3s, -webkit-box-shadow .3s;
  -webkit-transition: box-shadow .3s, border .3s;
  transition: box-shadow .3s, border .3s;
  border: none;
  border-bottom: 1px solid #9e9e9e;
  border-radius: 0;
  outline: none;
  background-color: transparent;
  box-shadow: none;
  color: rgb(153, 153, 153);
}
.input-field label {
  font-size: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  cursor: text;
  -webkit-transition: color .2s ease-out, -webkit-transform .2s ease-out;
  transition: color .5s ease-out, -webkit-transform .2s ease-out;
  -webkit-transition: color .2s ease-out, -webkit-transform .5s ease-out;
  transition: color .2s ease-out, -webkit-transform .5s ease-out;
  transition: transform .5s ease-out, color .2s ease-out;
  transition: transform .5s ease-out, color .2s ease-out, -webkit-transform .5s ease-out;
  transition: transform .5s ease-out, color .2s ease-out, -webkit-transform .2s ease-out;
  -webkit-transform: translateY(12px);
  transform: translateY(12px);
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  text-align: left;
  text-align: initial;
  color: #9e9e9e;
  -webkit-transition: .5s!important;
  transition: .5s!important;
  pointer-events: none;
}
