/*------------------------Step Progress bar css------------------------- */
.containerste {
  width: 100%;
}

.progressbar {
  counter-reset: step;
}
.progressbar li {
  list-style: none;
  display: inline-block;
  width: 23.33%;
  position: relative;
  text-align: center;
  cursor: pointer;
}
.progressbar li:before {
  content: counter(step);
  counter-increment: step;
  width: 30px;
  height: 30px;
  line-height : 30px;
  border: 1px solid #d14b07;
  border-radius: 100%;
  display: block;
  text-align: center;
  margin: 0 auto 10px auto;
  background: #d14b07!important;
}
.progressbar li:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background-color:#d14b07;
  top: 15px;
  left: -45%;
  z-index : 1;
}
.progressbar li:first-child:after {
  content: none;
}
/* .progressbar li.active {
  color: #d14b07;
} */
.progressbar li.active:before {
  border-color: #d14b07;
  contain: ''
} 
.progressbar li.active + li:after {
  background-color: #d14b07;
}
